import React, { Component } from 'react';
import './App.css';

import Wilddog from 'wilddog'
//var classNames = require('classnames');
import classNames from 'classnames'

classNames('foo', 'bar'); // => 'foo bar'
const config = {
    syncURL: "https://modemcu.wilddogio.com" //输入节点 URL
};
Wilddog.initializeApp(config);
const ref = Wilddog.sync().ref().child('allInOne');
const tickRef = Wilddog.sync().ref().child('tick');

const date2Str = (date)=>{
  return `${String(date.getMonth()+1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')} ${String(date.getHours()).padStart(2, '0')}:${String(date.getMinutes()).padStart(2,'0')} `
}


class App extends Component {
  constructor(props){
    super(props)
    this.state = {
      list : [],
      tick: 0,
    }

    ref.on('value', snap=>{
      let olists = snap.val()
      let lists  = Object.keys(olists).map((i)=>(Object.assign(olists[i], {key: i})))
      lists.sort((a, b)=>(b.date-a.date))
      this.setState({list: lists})
    })

    tickRef.on('value', snap=>{
      let date = snap.val()
      console.log(date)
      this.setState({tick: date})
    })

  }


  render() {


    const {list, tick} = this.state
    const li = list.map((item,index)=>{
      return <tr key={item.id}>
      {/*<td>{item.id}</td>*/}
        <td>{item.from}</td>
        <td>{date2Str(new Date(item.date))}</td>
        <td className={classNames({err: item.priceInfo>200 && item.priceInfo<500})}>{item.priceInfo}</td>
        <td >{item.area}</td>
        <td>{parseFloat(item.priceInfo/ item.area).toFixed(2)}</td>

        <td>{item.addressInfo}</td>
        <td><a href={item.href}>{item.title}</a></td>
        <td>{item.followInfo}</td>
      </tr>
    })

    return (
      <div className="App">
        <p>updated at: {date2Str(new Date(tick))}</p>
          <table>
          {li}
          </table>
      </div>
    );
  }
}

export default App;



